<template>
	<view class="commodity">
		<view v-if="haveData == 0" class="no-data">
			<image src="../../static/imgs/common/no.png" mode="aspectFill"></image>
			<text style="font-size: 30rpx">�����ղ��˲ţ��Ͻ�ȥ�ղ��������˲Ű�~</text>
		</view>
		<view v-else>
			<view class="every" v-for="(item,index) in favoriteslist" :key="index" @longtap="openDelPage(item)"
				@click="goUserDetails(item)">
				<view class="every-four">
					<image class="four-img" :src="imgUrl + '/' + item.headPic" mode="aspectFill"></image>
					<view class="four-name">
						{{item.fullName}}
					</view>
				</view>
			</view>
			<uni-popup ref="delPopup" type="center">
				<view class="del-popup-container">
					<view class="popup-title">
						��ܰ��ʾ
					</view>
					<view class="popup-main">
						ȷ�϶ԡ�{{selName}}��ȡ������Ȥ�� ��
					</view>
					<view class="popup-button">
						<view class="cancle" @click="cancleDel">
							ȡ��
						</view>
						<view class="sure" @click="sureDel">
							ȷ��
						</view>
					</view>
				</view>
			</uni-popup>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				imgUrl: this.$baseUrl,
				haveData: 1,
				page: 1,
				favoriteslist: [],

				selList: [],

				selName: '',

			};
		},
		onLoad() {
			this.favorites();
		},
		onPullDownRefresh() {
			this.favoriteslist = [];
			this.favorites();
			uni.stopPullDownRefresh();
		},
		methods: {
			// ȡ��
			cancleDel() {
				this.$refs.delPopup.close();
			},
			// ȷ��
			sureDel() {
				this.clickclose();
				this.$refs.delPopup.close();
			},
			// ������ɾ��ҳ��
			openDelPage(item) {
				console.log(item);
				this.selList = [];
				this.selList.push(item.id);
				this.selName = item.name;
				this.$refs.delPopup.open();
			},
			// �鿴�˲�����
			goUserDetails(item) {
				uni.navigateTo({
					url: "../index/userDetails?userId=" + item.id
				})
			},

			// ��ȡ�˲��ղ��б�
			favorites() {
				uni.showLoading({
					title: "������...",
				});
				uni.request({
					url: this.$baseUrl + "/favoritePersonList?page=" + this.page + '&pageNum=10',
					header: {
						"content-type": "application/json",
						token: uni.getStorageSync("token"),
					},
					complete: (res) => {
						uni.hideLoading();

						console.log("�˲��ղ��б�");
						console.log(res);

						this.favoriteslist = res.data.data.list;
						console.log(this.favoriteslist)

						if (this.favoriteslist.length == 0) {
							this.haveData = 0;
						} else {
							this.haveData = 1;
						}
					},
				});
			},
			// ɾ��
			clickclose() {
				console.log(this.selList);
				uni.request({
					method: "POST",
					url: this.$baseUrl + '/favoritePersonSave',
					data: {
						jobIdList: this.selList,
					},
					header: {
						"content-type": "application/json",
						token: uni.getStorageSync("token"),
					},
					success: (res) => {
						console.log(res.data);
						this.selList = [];
						this.favoriteslist = [];
						uni.showToast({
							title: "ɾ���ɹ�",
							icon: "none",
						});
						this.favorites();
					},
					fail: (err) => {
						uni.showToast({
							title: err.data.message,
							icon: "none",
						});
					},
				});
			},
		},
	};
</script>

<style lang="scss" scoped>
	.commodity {
		padding: 0 40rpx 0 40rpx;
		background-color: #fff;
		position: relative;

		.title {
			height: 100rpx;
			padding: 0 40rpx;
			position: fixed;
			left: 0;
			right: 0;
			display: flex;
			justify-content: space-between;
			align-items: center;
			font-size: 22rpx;
			background-color: #fff;
			z-index: 999;
		}

		.every {
			padding: 20rpx 0;
			border-bottom: 2rpx solid #ccc;

			.every-one {
				display: flex;
				justify-content: space-between;
				align-items: center;

				.one-left {
					font-size: 36rpx;
					font-weight: bold;
				}

				.one-right {
					display: flex;
					align-items: center;
					color: #85dbd0;
					font-size: 30rpx;
					font-weight: bold;

					.right-con {
						display: flex;
						align-items: center;

						.circle {
							width: 6rpx;
							height: 6rpx;
							margin: 0 5rpx;
							background-color: #85dbd0;
							border-radius: 50%;
						}
					}
				}
			}

			.every-two {
				margin-top: 10rpx;
				text-align: left;
				color: #A9A5A0;
				font-size: 24rpx;
			}

			.every-three {
				margin-top: 20rpx;
				display: flex;

				.three {
					padding: 10rpx;
					margin-right: 10rpx;
					background-color: #f9f9f9;
					font-size: 22rpx;
					color: #A9A5A0;
				}
			}

			.every-four {
				margin-top: 20rpx;
				display: flex;
				align-items: center;

				.four-img {
					width: 60rpx;
					height: 60rpx;
					margin-right: 20rpx;
					border-radius: 50%;
				}

				.four-name {
					font-size: 22rpx;
					color: #A9A5A0;
				}
			}
		}

		.del-popup-container {
			width: 580rpx;
			margin: 0 auto;
			padding: 40rpx;
			background-color: #fff;
			border-radius: 10rpx;
			text-align: left;

			.popup-title {
				font-size: 38rpx;
				font-weight: bold;
			}

			.popup-main {
				margin-top: 40rpx;
				color: #A9A5A0;
				font-size: 24rpx;
			}

			.popup-button {
				margin-top: 80rpx;
				display: flex;
				justify-content: flex-end;
				font-size: 30rpx;
				font-weight: bold;

				.cancle {
					color: #A9A5A0;
				}

				.sure {
					margin-left: 80rpx;
					color: #85dbd0;
				}
			}
		}
	}
</style>
